import * as actions from '../redux/actionTypes';
export const setFlashSaleTimer = (dateStart, timeStart, dateEnd, timeEnd) => {

  const localFormatDate = inputDate => {
    return `${inputDate.slice(3, 5)}/${inputDate.slice(0, 2)}/${inputDate.slice(6)}`
  }

  return {
    type: actions.FLASHSALE_TIMER_SETED,
    payload: {
      startTimer: `${localFormatDate(dateStart)} ${timeStart}`,
      endTimer: `${localFormatDate(dateEnd)} ${timeEnd}`
    }
  }
}